<template>
  <div class="farm">
    <h1>{{farm.name}}</h1>
  </div>
</template>

<script>
import api from "../api/antos-api";
export default {
  name: "Farm",
  data() {
    return {
      farm: {}
    }
  },
  mounted() {
    api.get_farm(this.$route.params.id).then(resp => {
      this.farm = resp.data
    })
  }
}
</script>

